.TodoSearch{
    background: #a3b0d5;
    border-radius: 2px;
    border: 2px solid black;
    margin: 90px 24px 0 24px; /* Añadido margen superior de 50px */
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center; 
    font-family: 'Monserrat';
    font-weight: 400;
    color: black;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    margin-top: 10px; /* Ajusta este valor según sea necesario */
    border-radius: 50px; /* Bordes redondeados */
    
}

.TodoSearch::placeholder {
    color: black;
    font-family: 'Monserrat';
    font-weight: 400;
}

.TodoSearch:focus{
    outline-color: rgb(140, 148, 221);
}